<template>
  <div>
    <div class="todo-lists">
        <div 
            class="todo-item" 
            v-for="(item, index) in todoItems" 
            :key="index"
        >
           {{index + 1}} {{item.text}}  
        <button class="btn-delete" @click="removeTodo(index)">x</button>
        </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'TodoLists',
  props: [
    'todoItems'
  ],
  data() {
    return {
        
    }
  },
  methods: {
      removeTodo(index) {
          this.todoItems.splice(index, 1)
      },
  },
}
</script>

<style scoped>
    .todo-item {
        font-size: 18px;
        background-color: aquamarine;
        padding: 10px 5px;
        margin: 5px 0;
        display: flex;
        justify-content: space-between;
    }
    .btn-delete {
        padding: 5px 10px;
        border-radius: 50%;
        border: none;
        background-color: red;
        color: #ffff;
        font-size: 14px;
        font-weight: 700;
        opacity: .5;
        cursor: pointer;
        outline: none;
    }
    .btn-delete:hover {
        opacity: 1;
    }
</style>
