<template>
  <div>
    <div class="todo-input">
      <input type="text" v-model="todoValue" placeholder="Add todo">
      <button class="btn-add" @click="addTodo">add</button>
    </div>
    <TodoLists :todoItems="todoItems"/>
  </div>
</template>

<script>
import TodoLists from './TodoLists.vue'

export default {
  name: 'AddTodo',
  components: {
    TodoLists,
  },
  props: {
    
  },
  data() {
    return {
      todoValue: '',
      todoItems: [
             {text: 'To learn Vue.js', id: 0},
             {text: 'Go to the gym', id: 1},
             {text: 'Make dinner', id: 2}, 
        ],

    }
  },
  methods: {
    addTodo() {
      this.todoItems.push({
        text: this.todoValue,
        id: new Date().valueOf()
      });
      this.todoValue = ''
    },
  },
}
</script>

