  }
  .btn-add {
    padding: 10px 15px;
    background-color: rgb(0, 64, 138);
    border: none;
    color: #fff;
    font-weight: 700;
    cursor: pointer;
  }
  .btn-add:hover {
    opacity: .8;
  }
</style>
